.app-wrapper {
  margin: 0 auto;
  display: grid;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 150px auto;
  grid-template-areas:
    "h h"
    "n c";
  width: 1024px;
  height: 100vh;
  position: relative;
}

.app-wrapper-content {
  grid-area: c;
  background-color: #f7f6f1;
  padding: 20px;

}
